<template>
  <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">

          <div class="modal-header">
            Добавление канала
            <slot name="header">
              <button class="modal-close-button" @click="close">
                Close
              </button>
            </slot>
          </div>

          <div class="modal-body">
            <slot name="body">
              <input v-model="channel_name" placeholder="@your_channel">
              <div>
                <input v-model="channel_id" placeholder="Айди канала">
                <label>
                  <span class="question-mark" title="Узнайте ID канала, перекинув ссылку на канал боту @username_to_id_bot">?</span>
                </label>
              </div>
              
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer">
              default footer
              <button @click="submit">Создать</button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>

export default {
  data() {
    return {
      channel_name: '',
      channel_id: ''
    };
  },
  computed: {
    axios() {
      return this.$store.getters.getAxios;
    },
    userData() {
      return this.$store.getters.getUserData;
    },
    createChannelModal() {
      return this.$store.getters.getCreateChannelModal;
    },
  },
  methods: {
    close() {
      this.updateCreateChannelModal({
        ...this.createChannelModal,
        isShow: false
      })
    },
    async submit() {
      const res = await this.axios.post("/channel", {channel_name: this.channel_name, channel_id: this.channel_id, user_id: this.userData.id})
      if (res.status === 200) {
        await this.refreshChannels()
      }
      this.close();
    },
    async refreshChannels() {
      const data = await this.axios.get(`/channels/${this.userData.id}`)
      if (data.status === 200) {
        this.updateUserChannels(data.data.data)
      }
    },
    // комиты
    updateUserChannels(data) {
      this.$store.commit('setUserChannels', data)
    },
    updateCreateChannelModal(data) {
      this.$store.commit('setCreateChannelModal', data)
    },
  }
}
</script>

