/* eslint-disable */
import api from './api';
import { createStore } from 'vuex'
import createPersistedState from 'vuex-persistedstate';

const store = createStore({
  state() {
    return {
      axios: api,
      userTgData: {},
      userData: {},
      userChannels: [],
      curChannel: {},
      modals: {
        createChannel: {
          isShow: false,
          channel_name: null,
          channel_id: null,
          config: null,
        }
      }
    };
  },
  mutations: {
    setUserTgData(state, data) {
      console.log("update userTgData", data)
      state.userTgData = data
    },
    setUserData(state, data) {
      console.log("update userdata", data)
      state.userData = data
    },
    setUserChannels(state, data) {
      console.log("update userChannels", data)
      state.userChannels = data
    },
    setCurChannel(state, data) {
      console.log("update setCurChannel", data)
      state.curChannel = data
    },
    // modals
    setCreateChannelModal(state, data) {
      console.log("update:modal createChannelModal", data)
      state.modals.createChannel = data
    },
  },
  actions: {
    // asyncSetUserData(context, data) { // ненадо
    //   setTimeout(() => {
    //     context.commit('setUserData', data);
    //   }, 1000);
    // },
  },
  getters: {
    getUserTgData: (state) => state.userTgData,
    getUserData: (state) => state.userData,
    getUserChannels: (state) => state.userChannels,
    getCurChannel: (state) => {
      const res = JSON.parse(JSON.stringify(state.curChannel));
      if (res.config && res.config.postTimes) {
        res.config.postTimes = res.config.postTimes.map(time => convertTo24HourFormat(time));
      }
      return res
    },
    // modals
    getCreateChannelModal: (state) => state.modals.createChannel,
    getAxios: (state) => state.axios,
  },
});

function convertTo24HourFormat(time) {
  let [hours, minutes] = time.split(':');
  // Если время в 12:00 AM, возвращаем 00:00 и наоборот
  if (hours == '12') {
    hours = '00';
  } else if (hours == '00') {
    hours = '12';
  }
  return `${hours.toString().padStart(2, '0')}:${minutes}`;
}

const persistedStateOptions = {
  key: 'my-vuex-state', // ключ для сохранения в localStorage
  storage: window.localStorage, // можно использовать window.sessionStorage
};

createPersistedState(persistedStateOptions)(store);

export default store;
