<template>
  <div class="ChannelsComp">
    <CreateChannelModal v-if="createChannelModal?.isShow" name="modal" />
    <h3>Ваши каналы</h3>
    <div class="channels-buttons">
      <span><button @click="updateChannels()">Обновить</button></span>
      <span><button @click="showCreateChannelModal">Создать</button></span>
    </div>
    <div class="channel-list">
      <router-link :style="{background: getColorBackground(channel.status)}" class="channel" to="/channel" @click="setCurChannel(channel.id, channel.user_id)" v-for="channel in userChannels" :key="channel.id">
        <div class="channel-name">
          Канал: {{ channel.channel_name }}
        </div>
        <div class="error-reason" v-if="channel.status !== 'HEALTHY'">
          Ошибка: {{ getReasonText(channel.reason) }}
        </div>
      </router-link>
    </div>
    
  </div>
</template>

<script>
import CreateChannelModal from '../components/CreateChannelModal.vue'

export default {
  name: 'ChannelsComp',
  data() {
    return {
      showModal: false,
    };
  },
  components: {
    CreateChannelModal,
  },
  computed: {
    axios() {
      return this.$store.getters.getAxios;
    },
    userData() {
      return this.$store.getters.getUserData;
    },
    createChannelModal() {
      return this.$store.getters.getCreateChannelModal;
    },
    userChannels() {
      const items = this.$store.getters.getUserChannels;
      return items.sort((a, b) => {
        // Переместите элементы со статусом "CRITICAL" в начало списка
        if (a.status === 'CRITICAL' && b.status !== 'CRITICAL') return -1;
        if (a.status !== 'CRITICAL' && b.status === 'CRITICAL') return 1;
        
        // Переместите элементы со статусом "WARNING" в начало списка, если их статусы равны
        if (a.status === 'WARNING' && b.status !== 'WARNING') return -1;
        if (a.status !== 'WARNING' && b.status === 'WARNING') return 1;

        // Оставьте элементы со статусом, отличным от "CRITICAL" и "WARNING", в том же порядке
        return 0;
      });
    },
  },
  methods: {
    getColorBackground(status) {
      switch(status){
        case "HEALTHY":
          return "green"
        case "WARNING":
          return "yellow"
        case "CRITICAL":
          return "red"
      }
    },
    getReasonText(status) {
      switch(status){
        case "CANT_SEND_POSTS":
          return "У бота отсутствуют права на создание постов."
        case "CHANNEL_NOT_FOUND":
          return "Канал не найден."
      }
    },
    showCreateChannelModal() {
      this.updateCreateChannelModal({
        ...this.createChannelModal,
        isShow: true
      });
    },
    setCurChannel(id, user_id) {
      this.getCurChannel(user_id, id)
    },
    async getCurChannel(user_id, channel_id) {
      const res = await this.axios.get(`/user/${user_id}/channel/${channel_id}`)
      if (res.status === 200) {
        this.updateCurChannel(res.data.data)
      }
    },
    async updateChannels() {
      console.log("tes")
      const res = await this.axios.get(`/channels/${this.userData?.id}`)
      if (res.status === 200) {
        this.updateUserChannels(res.data.data)
      }
    },
    // комиты
    updateCurChannel(data) {
      this.$store.commit('setCurChannel', data);
    },
    updateCreateChannelModal(data) {
      this.$store.commit('setCreateChannelModal', data);
    },
    updateUserChannels(data) {
      this.$store.commit('setUserChannels', data);
    },
  },
};
</script>

<style lang="scss" scoped>
.channel {
  color: inherit;
  text-decoration: none;
  border: 1px solid brown;
  display: block;
  padding: 20px;
  border-radius: 5px;
  .error-reason {
    margin-top: 5px;
  }
  &-list {
    margin-top: 10px;
    max-width: 500px;
  }
  &-name {
    display: inline-block;
  }
  &:not(:first-child) {
    margin-top: 10px;
  }
}
</style>
