import { createRouter, createWebHistory } from 'vue-router'
import ChannelsComp from '../views/СhannelsView.vue';
import CurChannelView from '../views/CurChannelView.vue';

const routes = [
  {
    path: '/',
    name: 'ChannelsComp',
    component: ChannelsComp,
  },
  {
    path: '/channel',
    name: 'CurChannelView',
    component: CurChannelView,
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
