<template>
  <!-- <hello-test /> -->
  <div class="telegram-mini-app">
    <!-- <div class="test">{{ userData }}</div> -->
    
    <div class="profile">
      <h2>Профиль пользователя</h2>
      <p>Здравствуйте, {{`${userData?.first_name} ${userData?.last_name}`}}</p>
      <p>{{`@${userData?.username}`}}</p>
    </div>
    <router-view/>
  </div>
</template>
<div class=""></div>
<script>
import HelloTest from './components/HelloTest.vue';

/* eslint-disable */

export default {
  name: 'TelegramMiniApp',
  data() {
    return {
      telegramWebAppData: null,
      title: "Telegram Mini App",
      requestText: "",
      buttonDisabled: false,
      response: [],
      responseKey: 0,
    };
  },
  components: {
    HelloTest
},
  computed: {
    axios() {
      return this.$store.getters.getAxios;
    },
    userTgData() {
      return this.$store.getters.getUserTgData;
    },
    userData() {
      return this.$store.getters.getUserData;
    },
  },
  mounted() {
    this.getTgData()
    this.login()
  },
  methods: {
    getTgData() {
      if (window.Telegram && window.Telegram.WebApp && window.Telegram.WebApp.initData) {
        // const params = { "id": 2, "telegram_id": 1987357469, "config": {}, "first_name": "Dmitriy", "last_name": "Alexandrov", "username": "ditritus_h", "tg_api": null, "role": "USER", "end_subscription": null, "updatedAt": "2023-12-28T05:32:36.388Z", "createdAt": "2023-12-28T05:31:32.343Z" }
        // this.updateUserTgData(params)
        // window.Telegram.WebApp.initData
        
      } else {
        const params = this.stringToObject('query_id=AAEdq3R2AAAAAB2rdHb-RVel&user={"id":1987357469,"first_name":"Dmitriy","last_name":"Alexandrov","username":"ditritus_h","language_code":"ru","allows_write_to_pm":true}&auth_date=1703739367&hash=4c71272061cb0c6abb6832d9167b1d8a6f846a44e02177d819cc442e990aba1d}')
        this.updateUserTgData(params)
      }
    },
    getTzh() {
      // Получаем текущее время
      let now = new Date();
      // Получаем смещение временной зоны в минутах
      let offsetMinutes = now.getTimezoneOffset();
      // Переводим смещение в часы
      let offsetHours = offsetMinutes / 60;
      // Возвращаем часовой пояс
      return -offsetHours;
    },
    async login() {
      const res = await this.axios.post("/authorize", {telegram_id: this.userTgData?.user.id, first_name: `${this.userTgData?.user.first_name}`, last_name: `${this.userTgData?.user.last_name}`, username: `${this.userTgData?.user.username}`, tzh: this.getTzh()})
      if (res.status === 200) {
        this.updateUserData(res.data.data)
        this.refreshChannels()
      }
    },
    async refreshChannels() {
      console.log("wer")
      const elem = this.userData?.id
      if (elem === undefined) {
        this.$router.push('/');
        return
      }
      
      const data = await this.axios.get(`/channels/${this.userData?.id}`)
      if (data.status === 200) {
        this.updateUserChannels(data.data.data)
      }
    },
    // комиты
    updateUserTgData(data) {
      this.$store.commit('setUserTgData', data);
    },
    updateUserData(data) {
      this.$store.commit('setUserData', data);
    },
    updateUserChannels(data) {
      this.$store.commit('setUserChannels', data);
    },
    // тулсы
    stringToObject(queryString) {
      queryString = decodeURIComponent(queryString);
      const parts = queryString.split('&');
      const params = {};
      parts.forEach(part => {
        const [key, value] = part.split('=');
        if (key === 'user') {
          params[key] = JSON.parse(decodeURIComponent(value));
        } else {
          params[key] = decodeURIComponent(value);
        }
      });
      return params
    },
  },
};
</script>

