<template>
  <div>
    <!-- <div>{{ сurChannel }}</div> -->
    <router-link to="/">Назад</router-link>
    
    <h2>Канал {{ сurChannel.channel_name }}</h2>
    
    <div class="status">
      <h3>Состояние: {{ сurChannel.status == "HEALTHY" ? "Рабочий" : `Есть неисправность: ${сurChannel.status}:${сurChannel.reason}` }}</h3>
    </div>
    <button @click="fastPost()">Сделать быстрый пост</button>
    <button @click="showSettings = !showSettings">Открыть настройки канала</button>
    <div v-show="showSettings" class="settings">
      <div class="settings-theme">
        Тематика: <input type="text" v-model="settings_theme" placeholder="Одним - двумя словами про тематику канала">
      </div>
      <div class="settings-symbols">
        Количество символов: <input type="number" min="0" v-model="settings_symbols" placeholder="Сколько в среднем будет символов в посте">
      </div>
      <div class="settings-expression">
        Экспрессия: <input type="range" min="0" max="100" v-model="settings_expression">
      </div>
      <div class="settings-orig">
        Оригинальность: <input type="checkbox" v-model="settings_orig">
      </div>
      <div class="settings_expert">
        <div>Экспертность:</div>
        <textarea
          v-model="settings_expert"
          placeholder="Например: Представь, что ты таролог с двадцатилетним стажем и обладаешь всеми знаниями, опытом и знаешь даже самые неочевидные лайфхаки."
        ></textarea>
      </div>
      <div class="settings-gender">
        <input type="radio" id="male" value="male" v-model="settings_gender" />
        <label for="male">Мужчина</label>
        <input type="radio" id="female" value="female" v-model="settings_gender" />
        <label for="female">Женщина</label>
      </div>
      <div class="settings-smiles">
        Смайлики: <input type="checkbox" v-model="settings_smiles">
      </div>
      <div class="settings-slang">
        <div>Сленг:</div>
        <input type="radio" id="young" value="young" v-model="settings_slang" />
        <label for="young">Молодежный</label>
        <input type="radio" id="expert" value="expert" v-model="settings_slang" />
        <label for="expert">Эксперта</label>
        <input type="radio" id="science" value="science" v-model="settings_slang" />
        <label for="science">Научный</label>
        <input type="radio" id="modern" value="modern" v-model="settings_slang" />
        <label for="modern">Современный</label>
        <input type="radio" id="girl" value="girl" v-model="settings_slang" />
        <label for="girl">Девочки</label>
        <input type="radio" id="malee" value="male" v-model="settings_slang" />
        <label for="malee">Мужчины</label>
      </div>
      <div>
        <label for="postCount">Количество постов:</label>
        <input type="number" id="postCount" v-model="postCount" min="1" @change="() => updatePostTimes()">
        
        <div v-for="(post, index) in postTimes" :key="index">
          <label :for="'postTime_' + index">Время для поста {{ index + 1 }}:</label>
          <input type="time" value="22:00" :id="'postTime_' + index" v-model="postTimes[index]" @change="(e) => updatePostTimes(e, index)" >
        </div>
      </div>
      <button :disabled="isLoading" @click="handleSave">{{ isLoading ? 'Загрузка...' : 'Сохранить' }}</button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      isLoading: false,
      showSettings: false,
      postCount: 1,
      postTimes: ['00:00'],
      //settings
      settings_theme: '',
      settings_symbols: '',
      settings_expression: '',
      settings_orig: false,
      settings_expert: '',
      settings_gender: '',
      settings_smiles: false,
      settings_slang: '',
    }
  },
  mounted() {
    this.init()
  },
  computed: {
    axios() {
      return this.$store.getters.getAxios;
    },
    сurChannel() {
      console.log("return cur channel", this.$store.getters.getCurChannel)
      return this.$store.getters.getCurChannel;
    },
    userData() {
      return this.$store.getters.getUserData;
    },
  },
  watch: {
    сurChannel(newVal, oldVal) {
      this.postInit(newVal);
    }
  },
  methods: {
    async fastPost() {
      const res = await this.axios.post(`/post`, {
        channel_id: this.сurChannel.id,
        time: new Date()
      })
    },
    init() {
      this.settings_theme = this.сurChannel.config?.theme || ''
      this.settings_symbols = this.сurChannel.config?.symbols || ''
      this.settings_expression = this.сurChannel.config?.expression || ''
      this.settings_orig = this.сurChannel.config?.orig || false
      this.settings_expert = this.сurChannel.config?.expert || ''
      this.settings_gender = this.сurChannel.config?.gender || ''
      this.settings_smiles = this.сurChannel.config?.smiles || false
      this.settings_slang = this.сurChannel.config?.slang || ''
      this.postCount = this.сurChannel.config?.postTimes?.length || 1
      this.postTimes = this.сurChannel.config?.postTimes || ['12:00'];
    },
    postInit(newVal) {
      this.settings_theme = newVal.config.theme || ''
      this.settings_symbols = newVal.config.symbols || ''
      this.settings_expression = newVal.config.expression || ''
      this.settings_orig = newVal.config.orig || false
      this.settings_expert = newVal.config.expert || ''
      this.settings_gender = newVal.config.gender || ''
      this.settings_smiles = newVal.config.smiles || false
      this.settings_slang = newVal.config.slang || ''
      this.postCount = newVal.config.postTimes?.length || 1
      this.postTimes = newVal.config.postTimes || ['12:00'];
    },
    updatePostTimes(e, index) {
      if (e) {
        // console.log("index", index, e.target.value)
      }
      
      if (!Number.isInteger(index)) {
        while (this.postTimes.length < this.postCount) {
          this.postTimes.push('00:00');
        }

        if (this.postTimes.length > this.postCount) {
          this.postTimes = this.postTimes.slice(0, this.postCount);
        }
      }
    },
    convertTo24HourFormat(time) {
      let [hours, minutes] = time.split(':');
      // Если время в 12:00 AM, возвращаем 00:00 и наоборот
      if (hours == '12') {
        hours = '00';
      } else if (hours == '00') {
        hours = '12';
      }
      return `${hours.toString().padStart(2, '0')}:${minutes}`;
    },
    convertToData(time) {
      let [hours, minutes] = time.split(':');
      const date = new Date();
      date.setSeconds(0);
      date.setMilliseconds(0);
      date.setHours(hours);
      date.setMinutes(minutes);
      return date.getTime();
    },
    async handleSave() {
      this.isLoading = true
      let resTime = this.postTimes.map(time => this.convertTo24HourFormat(time));
      // const resTime = this.postTimes.map(time => this.convertToData(time)); 
      const toEdit = {
        ...this.сurChannel,
        config: {
          theme: this.settings_theme,
          symbols: this.settings_symbols,
          expression: this.settings_expression,
          orig: this.settings_orig,
          expert: this.settings_expert,
          gender: this.settings_gender,
          smiles: this.settings_smiles,
          slang: this.settings_slang,
          postTimes: resTime,
        }
      }
      const res = await this.axios.patch(`/user/${this.userData.id}/channel/${this.сurChannel.id}`, toEdit)
      if (res.status === 200) {
        this.updateCurChannel(res.data.data)
      }
      this.isLoading = false
    },
    // комиты
    updateUserData(data) {
      this.$store.commit('setUserData', data);
    },
    updateCurChannel(data) {
      this.$store.commit('setCurChannel', data);
    },
  }
};
</script>
